
#mall-header[data-v-e16b315a] { width: 100%; background: url(../../../static/images/mall_header_bg.png) no-repeat; background-size: 100% 100%; color: white; text-align: left;
}
.mall-header-content[data-v-e16b315a] { padding: 5px 10px; position: relative;
}
.userName[data-v-e16b315a] { 
	display: inline-block; 
